import { useState } from 'react';
import { useDispatch } from 'react-redux';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import swal from "sweetalert";
import { getAllPlans } from '../../redux/actions';
import axios from 'axios';

const btnEdit = <svg xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 512 512"><path d="M410.3 231l11.3-11.3-33.9-33.9-62.1-62.1L291.7 89.8l-11.3 11.3-22.6 22.6L58.6 322.9c-10.4 10.4-18 23.3-22.2 37.4L1 480.7c-2.5 8.4-.2 17.5 6.1 23.7s15.3 8.5 23.7 6.1l120.3-35.4c14.1-4.2 27-11.8 37.4-22.2L387.7 253.7 410.3 231zM160 399.4l-9.1 22.7c-4 3.1-8.5 5.4-13.3 6.9L59.4 452l23-78.1c1.4-4.9 3.8-9.4 6.9-13.3l22.7-9.1v32c0 8.8 7.2 16 16 16h32zM362.7 18.7L348.3 33.2 325.7 55.8 314.3 67.1l33.9 33.9 62.1 62.1 33.9 33.9 11.3-11.3 22.6-22.6 14.5-14.5c25-25 25-65.5 0-90.5L453.3 18.7c-25-25-65.5-25-90.5 0zm-47.4 168l-144 144c-6.2 6.2-16.4 6.2-22.6 0s-6.2-16.4 0-22.6l144-144c6.2-6.2 16.4-6.2 22.6 0s6.2 16.4 0 22.6z" /></svg>


const FormEdit = ({ id, nombre, costo, duracion, contenido }) => {
    const dispatch = useDispatch();

    const [show, setShow] = useState(false);
    const [values, setValues] = useState({
        nombre,
        costo,
        duracion,
        contenido,
    });

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const handleChange = (event) => {
        const { name, value } = event.target;
        setValues({
            ...values,
            [name]: value,
        });
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
        const { nombre, costo, duracion, contenido } = values;

        try {
            const { data } = await axios.put(`https://backend-6du3.onrender.com/plans/${id}`, {
                nombre,
                costo,
                duracion,
                contenido,
            });
            swal({
                title: 'Plan Actualizado Exitosamente',
                icon: 'success',
            });
            handleClose();
            dispatch(getAllPlans());
        } catch (error) {
            console.log(error);
        }
    };

    return (
        <>
            <Button variant="none" onClick={handleShow}>
                {btnEdit}
            </Button>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Editar Plan</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                            <Form.Label>Tipo de Plan</Form.Label>
                            <Form.Control
                                type="text"
                                name='nombre'
                                placeholder="Ejemplo: Gratis, Premium"
                                value={values.nombre}
                                onChange={handleChange}
                                autoFocus
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                            <Form.Label>Costo $</Form.Label>
                            <Form.Control
                                type="number"
                                name='costo'
                                placeholder="Ingrese el monto..."
                                value={values.costo}
                                onChange={handleChange}
                                min={0}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                            <Form.Label>Duracin (Das)</Form.Label>
                            <Form.Control
                                type="number"
                                name='duracion'
                                placeholder="Ingrese la cantidad de das."
                                value={values.duracion}
                                onChange={handleChange}
                                min={0}
                            />
                        </Form.Group>
                        <Form.Group
                            className="mb-3"
                            controlId="exampleForm.ControlInput1"
                        >
                            <Form.Label>Descripcin</Form.Label>
                            <Form.Control
                                as="textarea"
                                rows={3}
                                name='contenido'
                                placeholder="Ingrese la descripcin del plan..."
                                value={values.contenido}
                                onChange={handleChange}
                            />
                        </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Cancelar
                    </Button>
                    <Button variant="primary" type='submit' onClick={handleSubmit}>
                        Guardar Cambios
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default FormEdit